import { Component, OnInit, EventEmitter } from '@angular/core';
import { Router } from '@angular/router';

import { Analyzes } from '../analyzes';
import { AnalyzesService } from '../analyzes.service';

import { ToolbarService } from '../../shared/toolbar.service';
import { ToastService } from '../../shared/toast.service';
import { PaginationService } from '../../shared/pagination.service';
import { MaterializeAction } from 'angular2-materialize/src';
import { HelpPanelService } from '../../shared/help-panel.service';


@Component({
  selector: 'app-analyzes',
  templateUrl: './analyzes.component.html',
  styleUrls: ['./analyzes.component.css']
})
export class AnalyzesComponent implements OnInit {
  public analyzes: Analyzes[];
  public currentItem: Analyzes;
  public modalDeleteActions = new EventEmitter<string|MaterializeAction>();
  public modalMergeActions = new EventEmitter<string|MaterializeAction>();
  public merge: Analyzes[] = [];
  public mergeMode: boolean = false;
  public filter: any;

  constructor(
    private _analyzesService: AnalyzesService,
    private _toastService: ToastService,
    private _toolbar: ToolbarService,
    private _router: Router,
    public _pager: PaginationService,
    private _helpPanel: HelpPanelService
  ) { }

  ngOnInit() {
    let search = '';

    this._helpPanel.setAnchor('settings');

    this._toolbar.change({
      title: "Список анализов",
      name: "analyzes",
      actions: {
        //add: () => this._router.navigate(['analyzes/add']),
        search: (searchText) => {
          this._pager.text = searchText;
          this._pager.currentPage = 1;
          this.getAnalyzes();
        },
        done: {
          ok: () => {
          this.modalMergeActions.emit({action:"modal",params:['open']});
          },
          close: () => {
            this.mergeMode = false;
          }
        }
      },
      actionsPosition: {
        extend: ['search'] //'add', 
      }
    });

    this._pager.setAction((currentPage) => {
      this.getAnalyzes();
    });
    this._pager.getPager();
    
    this.filter = {
      labs: {helix: false, invitro: false, cmd: false},
      nPerPage: 30,
      currentPage: 1,
      totalPages: 0
    };
      
    this.getAnalyzes();
  }

  openModal(item) {
    this.currentItem = item;
    this.modalDeleteActions.emit({action:"modal",params:['open']});
  }

  closeModal(name) {
    switch(name) {
      case 'delete':
        this.modalDeleteActions.emit({action:"modal",params:['close']});
        break;
      case 'merge':
        this.modalMergeActions.emit({action:"modal",params:['close']});
        break;
    }
  }

  deleteItem(item: Analyzes) {
    let self = this;

    this._analyzesService.remove(item._id)
      .subscribe((data: any) => {
        if(data.status == 'ok') {
          this.analyzes.find((analyz: Analyzes, index) => {
            if(item._id == analyz._id) {
              self.currentItem = null;
              self.analyzes.splice(index, 1);
              return true;
            }
          });
          this._toastService.toast("Анализ удален");
        }
      });
  }

  mergeModeStart(): void {
    this._toolbar.setMode('done');
    this.mergeMode = true;
    this.merge.map((item: any) => delete item.merge);
    this.merge = [];
  }

  mergeItems(): void {
    this._analyzesService
      .merge(this.merge)
      .subscribe((data) => {
        this.mergeMode = false;
        this._toolbar.setMode('default');
        this.getAnalyzes();
      });
  }

  clickItem(item): void {
    this.currentItem = item;
    let deleted = false;
    if(this.mergeMode) {
      let domEl = <any>document.querySelector('#checkField'+item._id);
    
      this.merge.map((mergeItem: any, index) => {
        if(item._id == mergeItem._id) {
          this.merge.splice(index, 1);
          deleted = true;
        }
      });
      
      if(this.merge.length < 3) {
        if(!deleted) this.merge.push(item);
      } else {
        domEl.checked = false;
      }
    }
  }

  keyDownFunction(event?): void {
    if(event.keyCode == 13) {
      this.filterGetAnalyzes();
    }
  }

  public filterGetAnalyzes() {
    this.getAnalyzes(true);
    this._helpPanel.toggle();
  }

  private getAnalyzes(toStart?) {
    let params = this._pager.getParams();
    params.labs = this.filter.labs;

    if(toStart) this._pager.currentPage = 1;
    this._analyzesService.get(params)
      .subscribe((data: any) => {
        this.analyzes = [];
        data.items.map((item) => {
          let analyz = new Analyzes(item);
          this.analyzes.push(analyz);
        });

        this._pager.setParams({countItems: data.count});
        this._pager.getPager();
      });
  }
}



